import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Cancel from "../../assets/images/closeNew.svg";
const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: "auto",
    backgroundColor: "#ffffff",
    boxShadow: theme.shadows[2],
    height: "auto",
    margin: "auto",
    top: "20%",
    padding: "20px",
  },
  nopadding: {
    position: "absolute",
    backgroundColor: "#ffffff",
    margin: "auto",
    top: "3%",
  },
  modalStyle1: {
    position: "absolute",
    top: "10%",
    left: "10%",
    overflow: "scroll",
    height: "100%",
    display: "block",
  },
}));
export default function SimpleModal(props) {
  const classes = useStyles();
  return (
    <div>
      <Modal
        open={props.isShowModal}
        onClose={props.onCloseModal}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        onBackdropClick={props.onCloseModal}
        disableAutoFocus={true}
        disableEnforceFocus={true}
        disableRestoreFocus={true}
        className={classes.modalStyle1}
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <div className={props.preview ? classes.nopadding : classes.paper}>
          {props.showClose && (
            <div className="close" onClick={props.onCloseModal}>
              <img src={Cancel} width="20px" />
            </div>
          )}
          {props.children}
        </div>
      </Modal>
    </div>
  );
}
