import React from "react";
import { connect } from "react-redux";
import * as Actions1 from "../../store/User/UserAction";
import { bindActionCreators } from "redux";
import TableComponent from "../../components/Controls/Table/Table";
import { Card } from "@material-ui/core";
import Button from "../../components/Controls/Button/Button";
import PersonAddOutlinedIcon from "@material-ui/icons/PersonAddOutlined";
import ImportExportIcon from "@material-ui/icons/ImportExport";
import rounder from "../../assets/images/Group 1240.svg";
import pencil from "../../assets/images/Group 1245.svg";
import "./userManagement.css";
import PrintModal from "../../components/Atoms/printModal";
import CustomizedInputs from "../../components/Atoms/CustomizedInputs/CustomizedInputs";
import SelectInput from "../../components/Atoms/SelectInput/SelectInput";
import CsvDownloader from "react-csv-downloader";
import Modal from "@material-ui/core/Modal";
import { Link } from "react-router-dom";
import Button1 from "@material-ui/core/Button";
import ReactGA from "react-ga";

class UserManagement extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      page: 1,
      limit: 5,
      email: "",
      Username: "",
      password: "",
      role: "",
      fromEdit: false,
      currentId: "",
      roleOptions: [
        { value: "Admin", label: "Admin" },
        { value: "SuperAdmin", label: "SuperAdmin" },
        { value: "HR", label: "HR" },
        { value: "Designer", label: "Designer" },
      ],
      isShowModal: false,
      isShowDeleteModal: false,
      id: "",
    };
  }

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    const payload = {
      page: this.state.page,
      limit: this.state.limit,
    };
    this.props.UserAction.getUser(payload);
  }

  static getDerivedStateFromProps(nextProps, currentState) {
    if (
      nextProps &&
      nextProps.UserReducer &&
      nextProps.UserReducer.isUserDataAdded
    ) {
      nextProps.UserReducer.isUserDataAdded = false;
      currentState.isShowModal = false;
      const payload = {
        page: currentState.page,
        limit: currentState.limit,
      };
      nextProps.UserAction.getUser(payload);
    }

    if (
      nextProps &&
      nextProps.UserReducer &&
      nextProps.UserReducer.isDeletedUserData
    ) {
      nextProps.UserReducer.isDeletedUserData = false;
      currentState.isShowDeleteModal = false;
      const payload = {
        page: currentState.page,
        limit: currentState.limit,
      };
      nextProps.UserAction.getUser(payload);
    }
    if (
      nextProps &&
      nextProps.UserReducer &&
      nextProps.UserReducer.isUserDataUpdated
    ) {
      nextProps.UserReducer.isUserDataUpdated = false;
      currentState.isShowModal = false;
      const payload = {
        page: currentState.page,
        limit: currentState.limit,
      };
      nextProps.UserAction.getUser(payload);
    }
  }

  handlePagination = async (e) => {
    await this.setState({ page: e.selected + 1 });
    const payload = {
      page: this.state.page,
      limit: this.state.limit,
    };
    this.props.UserAction.getUser(payload);
  };

  addUser = async () => {
    await this.setState({
      Username: "",
      email: "",
      password: "",
      role: "",
    });
    this.setState({ isShowModal: true, fromEdit: false });
  };

  addLabelTagName = (e) => {
    this.setState({
      labelTagName: e.target.value,
    });
  };

  handleChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };

  addRole = (e) => {
    this.setState({
      role: e.value,
    });
  };

  handleCancel = () => {
    this.setState({
      isShowModal: false,
    });
  };

  handleSave = () => {
    const { Username, email, password, role, currentId } = this.state;
    if (this.state.fromEdit === false) {
      const payload = {
        name: Username,
        email: email,
        password: password,
        role: role,
      };
      this.props.UserAction.addUser(payload);
    } else {
      if (this.state.password !== "") {
        const payload = {
          name: Username,
          email: email,
          password: password,
          role: role,
          id: currentId,
        };
        this.props.UserAction.updateUser(payload);
      } else {
        const payload = {
          name: Username,
          email: email,
          role: role,
          id: currentId,
        };
        this.props.UserAction.updateUser(payload);
      }
    }
  };

  deleteClick = (val) => {
    this.setState({
      isShowDeleteModal: true,
      id: val.id,
    });
  };

  handleCloseDeleteModal = () => {
    this.setState({ isShowDeleteModal: false });
  };

  handleDelete = () => {
    const payload = {
      id: this.state.id,
    };
    this.props.UserAction.deleteUserData(payload);
  };
  editClick = (val) => {
    this.setState({
      Username: val.name,
      email: val.email,
      role: val.role,
      isShowModal: true,
      fromEdit: true,
      currentId: val.id,
    });
  };

  render() {
    let data = [];
    let csvData = [];

    let totalPage = 1;
    if (
      this.props &&
      this.props.UserReducer &&
      this.props.UserReducer.getUserData &&
      this.props.UserReducer.getUserData.data &&
      this.props.UserReducer.getUserData.data.users
    ) {
      let data1 = [];
      totalPage =
        this.props.UserReducer.getUserData.data.totalUser / this.state.limit;
      this.props.UserReducer.getUserData.data.users.map((item, index) => {
        data1.push({
          number: index + 1,
          name: item.name,
          role: item.role,
          id: item._id,
          email: item.email,
          password: item.password,
        });
        csvData.push({
          name: item.name,
          role: item.role,
          email: item.email,
          id: item._id,
        });
      });

      data = data1;
    }

    let inboundDatacolumn = [
      {
        Header: " ",
        accessor: "number",
        minWidth: 30,
      },

      {
        Header: "Name",
        accessor: "name",
        minWidth: 80,
        className: "header-name",
      },
      {
        Header: "Role",
        accessor: "role",
        minWidth: 80,
        className: "header-role",
      },
      {
        Header: "Action",
        accessor: "action",
        minWidth: 80,
        className: "header-action",
        Cell: (val) => {
          return (
            <div className='user-button-user'>
              <Button
                className='user-btn-pencil'
                onClick={(e) => this.editClick(val.original)}
                endIcon={pencil}
              ></Button>
              <Button
                className='user-btn-rounded'
                onClick={(e) => this.deleteClick(val.original)}
                endIcon={rounder}
              ></Button>
            </div>
          );
        },
        //Cell: (props)=> <div>{props.original.action===true?<div><CreateRoundedIcon/><IndeterminateCheckBoxRoundedIcon/></div>:<CreateRoundedIcon/>}</div>
      },
    ];

    return (
      <div>
        <Card id='user-card'>
          <div className='user-btn-3'>
            <div className='user-add'>
              <Button className='user-btns' onClick={this.addUser}>
                <span className='user-btns-span'>
                  <PersonAddOutlinedIcon />
                </span>
                Add User
              </Button>
            </div>
            <div className='user-export'>
              <CsvDownloader
                filename='UserReportData'
                target='_blank'
                separator=''
                wrapColumnChar={"\t"}
                datas={csvData}
              >
                <Button className='user-btns'>
                  <span className='user-btns-span'>
                    <ImportExportIcon />
                  </span>{" "}
                  Export
                </Button>
              </CsvDownloader>
            </div>
          </div>

          <div className='user-table'>
            <TableComponent
              data={data}
              columns={inboundDatacolumn}
              handlePageClick={(e) => {
                this.handlePagination(e);
              }}
              totalPage={totalPage}
            />
          </div>
        </Card>
        <div>
          <PrintModal
            isShowModal={this.state.isShowModal}
            className='add-modal'
            onCloseModal={() => this.setState({ isShowModal: false })}
            showClose={true}
          >
            <div className='hardware-modal'>
              <div>{this.state.fromEdit ? "Update User" : "Add User"}</div>
              <hr />
              <div className='modal-upper-div'>
                <div className='outer-div'>
                  <CustomizedInputs
                    placeholder='Enter  Name'
                    value={this.state.Username}
                    name='Username'
                    onChange={this.handleChange}
                  />
                </div>
                <div className='outer-div'>
                  <CustomizedInputs
                    placeholder='Enter Email'
                    value={this.state.email}
                    name='email'
                    onChange={this.handleChange}
                  />
                </div>
              </div>
              <div className='modal-middle-div'>
                <div className='outer-div'>
                  <CustomizedInputs
                    type='password'
                    name='password'
                    placeholder='Enter Password'
                    value={this.state.password}
                    onChange={this.handleChange}
                  />
                </div>
                <div>
                  <SelectInput
                    className='react-select role'
                    labelName='Role'
                    onChange={(e) => {
                      this.addRole(e);
                    }}
                    options={this.state.roleOptions}
                    placeholder={
                      this.state.role ? this.state.role : "Select Role"
                    }
                    value={this.state.role}
                  />
                </div>
              </div>

              <div>
                <div className='button-div'>
                  <Button className='cancel' onClick={this.handleCancel}>
                    Cancel
                  </Button>
                  <Button className='save' onClick={this.handleSave}>
                    {this.state.fromEdit ? "UPDATE" : "SAVE"}
                  </Button>
                </div>
              </div>
            </div>
          </PrintModal>
          <div>
            <Modal
              className='user-modal'
              open={this.state.isShowDeleteModal}
              onClose={this.handleCloseDeleteModal}
            >
              <div className='user-delete'>
                <p>Do you want to delete this user?</p>

                <Button1
                  className='user-button-delete'
                  onClick={this.handleCloseDeleteModal}
                >
                  <Link>No</Link>
                </Button1>

                <Button1
                  className='user-button-delete'
                  onClick={this.handleDelete}
                >
                  <Link className={this.handleDelete}>Yes</Link>
                </Button1>
              </div>
            </Modal>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    UserReducer: state.UserReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    UserAction: bindActionCreators(Actions1, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(UserManagement);
